//import React, {useState, useEffect, useCallback} from 'react';
import { useFetchData } from '../hooks/useFetchData'
import Image from './Image'
import Loading from './Loading'
import Search from './Search'

const Images = () => {

    const [data, loading, handleSubmit] = useFetchData();
    
    return (
        <> 
            <div className="mb-12">
                <Search handleSubmit={handleSubmit}/>
            </div> 

            {loading && <Loading />}    

            <div className="grid sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-x-6 gap-y-8">    
                {
                    data.map((img) => {
                        return <Image key={img.id} img={img.urls.regular} user={img.user} description={img.alt_description} permalink={img.links.html} target={"_blank"}/>
                    })
                }
            </div>
        </>
    )
}

export default Images
