import React, {useState, useEffect} from 'react'
import PropTypes from 'prop-types'
import Icon from './Icon'
// import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

const Rating = ({value}) => {

    const [color, setColor] = useState("text-yellow-300")

    const handleColor = (e) => {
        console.log(e)
    }

    useEffect(() => {
        if(value > 2){
            setColor("text-yellow-300")
        } else {
            setColor("text-red-300")
        }
    }, [value])

    return (
        <div className="flex mb-4 items-center">
            {/* <!-- rating value --> */}
            <span className={`text-xl font-bold ${color}`}>{value}</span>

            {/* <!-- rating stars --> */}
            <ul className="flex text-sm ml-2">
                <li className="mr-1"><Icon icon="star" icon_class={value >= 1 ? color : 'text-gray-300'} /></li>
                <li className="mr-1"><Icon icon="star" icon_class={value >= 2 ? color : 'text-gray-300'} /></li>
                <li className="mr-1"><Icon icon="star" icon_class={value >= 3 ? color : 'text-gray-300'} /></li>
                <li className="mr-1"><Icon icon="star" icon_class={value >= 4 ? color : 'text-gray-300'} /></li>
                <li className="mr-1"><Icon icon="star" icon_class={value === 5 ? color : 'text-gray-300'} /></li>
                {/* <li className="mr-1"><i className="fas fa-star text-yellow-300"></i></li>                 */}
            </ul>
            {/* <!-- users enrolled --> */}
            <p className="text-sm text-gray-500 ml-auto">
                <i className="fas fa-users"></i>
                10 usuarios
            </p>
        </div>
    )
}

Rating.propTypes = {
    value: PropTypes.number
}

export default Rating
