import {useState, useEffect, useCallback} from 'react';

export const useFetchData = () => {
    const [data, setData] = useState([]);    
    const [input, setInput] = useState("");
    const [loading, setLoading] = useState(true)
    // const [data, setData] = useState({
    //     urls: {
    //         regular: "",
    //     },
    //     user: {
    //         name: "",
    //     },
    //     links: {
    //         html: "",
    //     },
    // });   

    const getData = useCallback(
        async () => {
            // const res = await fetch("https://api.unsplash.com/photos/random/?client_id=sMq_JZHrxRjw0sQUZpX10-EkQD-E8b8r3In_XSSUwdY") // random image

            const key = "client_id=sMq_JZHrxRjw0sQUZpX10-EkQD-E8b8r3In_XSSUwdY";

            let url = `https://api.unsplash.com/photos/?${key}`;
            let per_page = 12;

            if(input !== ""){
                url = `https://api.unsplash.com/search/photos/?page=2&query=${encodeURI(input)}&per_page=${per_page}&${key}`;
            }

            setLoading(true)

            const res = await fetch(url) // collection

            const d = await res.json()

            if(d.results){
                // console.log(d)
                setData(d.results);
            } else {
                setData(d);
            }   

            setLoading(false)     
        }, [input] );     

    useEffect(() => {
        getData();              
    }, [getData]);    

    const handleSubmit = (e) => {
        e.preventDefault();

        //console.log(e.target[0].value);
        const text = e.target[0].value;

        setInput(text);
    }

    return [data, loading, handleSubmit];
    
}


