import React from 'react'
import Link from './Link'
import Copyright from './Copyright'

const Footer = () => {

    const first_nav = [
        { link: '#', text: 'Link 1' },
        { link: '#', text: 'Link 2' },
        { link: '#', text: 'Link 3' },
        { link: '#', text: 'Link 4' },
    ];

    const second_nav = [
        { link: '#', text: 'Link 1' },
        { link: '#', text: 'Link 2' },
        { link: '#', text: 'Link 3' },
        { link: '#', text: 'Link 4' },
    ];

    const third_nav = [
        { link: '#', text: 'Link 1' },
        { link: '#', text: 'Link 2' },
        { link: '#', text: 'Link 3' },
        { link: '#', text: 'Link 4' },
    ];

    return (
        // <!-- component -->
        <footer style={{ background: "#279DBA", borderBottom: "2px solid #F68D93" }} className="footer relative pt-1">
            <div className="container mx-auto px-6">

                <div class="sm:flex sm:mt-8">
                    <div class="mt-8 sm:mt-0 sm:w-full sm:px-8 flex flex-col md:flex-row justify-between">
                        <div class="flex flex-col">
                            <span class="font-bold text-gray-700 uppercase mb-2">Footer header 1</span>
                            {first_nav.map((nav) => (
                                <span class="my-2"><Link text={nav.text} href={nav.link}/></span>
                            ))}                           
                        </div>
                        <div class="flex flex-col">
                            <span class="font-bold text-gray-700 uppercase mt-4 md:mt-0 mb-2">Footer header 2</span>
                            {second_nav.map((nav) => (
                                <span class="my-2"><Link text={nav.text} href={nav.link}/></span>
                            ))}   
                        </div>
                        <div class="flex flex-col">
                            <span class="font-bold text-gray-700 uppercase mt-4 md:mt-0 mb-2">Footer header 3</span>
                            {third_nav.map((nav) => (
                                <span class="my-2"><Link text={nav.text} href={nav.link}/></span>
                            ))}   
                        </div>
                    </div>
                </div>
            </div>
            <div class="container mx-auto px-6">
                <div class="mt-16 border-t-2 border-gray-300 flex flex-col items-center">
                    <div class="sm:w-2/3 text-center py-6">
                        <Copyright author="StressOut" other_class="text-white"/>
                    </div>
                </div>
            </div>
        </footer>
    )
}

export default Footer
