import React from 'react'
import PropTypes from 'prop-types'
import Rating from './Rating'
import Tags from './Tags'
import Price from './Price'

const Card = ({img, user, description, permalink, target}) => {

    return (
        <article className="card flex flex-col">

                    {/* card image */}
                    <div className="h-36 overflow-hidden relative">                                    
                            <img className="h-36 w-full object-cover transition duration-300 transform hover:scale-125" src={img} alt="" />      
                    </div>

                    {/* card body */}
                    <div className="card-body flex-1 flex flex-col">
                        {/* <!-- resource type --> */}
                        <p className="text-sm text-gray-500 mb-2">Tipo curso</p>
                        {/* <!-- course title --> */}
                        <a href={permalink} target={target}>
                            <h2 className="card-title">{description}</h2>
                        </a>
                        <p className="text-gray-500 text-sm mb-2 mt-auto">{user.name}</p>
                        <Rating value={3}/>
                        <Price value={19.99} currency="USD"/>                     
                        <Tags />                         
                        <div className="flex justify-end">
                            <span className="text-gray-500 text-xs" title="Modalidad">B-Learning</span>
                        </div>
                    </div>
                </article>
    )
}

Card.propTypes = {
    img: PropTypes.string,
    user: PropTypes.object,
    description: PropTypes.string,
    permalink: PropTypes.string,
    target: PropTypes.string
}

export default Card
