import React, {useState, useEffect} from 'react'
import PropTypes from 'prop-types'

const Copyright = ({author, other_class}) => {

    const [customClass, setCustomClass] = useState('')

    useEffect(() => {
        if(other_class){
            setCustomClass(other_class)
        }
    }, [other_class])

    return (
        <p className={`text-sm font-bold mb-2 ${customClass}`}>
            &copy; {new Date().getFullYear()} {author}
        </p>
    )
}

Copyright.propTypes = {
    text: PropTypes.string,
    taildwind_class: PropTypes.string
}

export default Copyright
