import React from 'react'
import PropTypes from 'prop-types'

const ButtonCommand = ({text, color}) => {
  return (
    <>        
      <a style={{ backgroundColor: `${color}` }} href="#" className="ml-8 whitespace-nowrap inline-flex items-center justify-center px-4 py-2 border border-transparent rounded-md shadow-sm text-base font-medium text-white bg-indigo-600 hover:bg-indigo-700">
        {text}
      </a>
    </>
  )
}

ButtonCommand.propTypes = {
  text: PropTypes.string
}

export default ButtonCommand
