import React from 'react'
import PropTypes from 'prop-types'

const Tag = ({text, link}) => {
    return (
        <a href="" data-toggle="tooltip" data-placement="top" className="mr-2 bg-gray-300 text-gray text-sm p-1 rounded  leading-none flex items-center focus:outline-none" title="">
            {text}
        </a>
    )
}

Tag.propTypes = {
    text: PropTypes.string,
    link: PropTypes.string
}

export default Tag
