import React from 'react'
import PropTypes from 'prop-types'

const Link = ({text, href}) => {
    return (
        <>
            <a href={href} className="whitespace-nowrap text-base font-medium text-gray-500 hover:text-gray-900 cursor-pointer">
                {text}
            </a>
        </>
    )
}

Link.propTypes = {
    text: PropTypes.string
}

export default Link
