import React from 'react'

const Spinner = () => {

    const commonStyle = {
        borderRightColor: "gray", 
        borderTopColor: "gray",         
    };

    return (       
        <svg style={commonStyle} className="relative inset-0 animate-spin h-16 w-16 rounded-full bg-transparent border-8 border-transparent border-opacity-50" viewBox="0 0 24 24"></svg>     
    )
}

export default Spinner
