import React from 'react'
import PropTypes from 'prop-types'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

const Icon = ({icon, icon_class}) => {
    return (
        <>
            <FontAwesomeIcon icon={icon} className={icon_class} />
        </>
    )
}

Icon.propTypes = {

}

export default Icon
